@charset 'utf-8';
@import 'base/normalize';
@import 'components/font';
@import 'components/color';
@import 'components/dialog';
@import 'components/loading';
@import 'components/button';
// @import 'components/twocode';
@import 'layout/weblayout';
// @import 'layout/grid';

// 初始化dialog
@include dialog;

.pepsi {
	height: inherit;
}

.pepsi-bg {
	position: absolute;
	z-index: -1;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;

	img {
		width: 100%;
		height: 100%;
		margin: 0 auto;
	}

	@media screen and (max-width: 320px) {
		img {
			height: 115%;
		}
	}
}



.pepsi-prize {
	// background-image: linear-gradient(45deg, #1777c9, #103589);
}

.pepsi-cot {
	height: 100%;
	position: relative;

	.two-code {
		position: relative;
		z-index: 1;
		top: 60%;
		width: 300px;
		height: 150px;
		margin: 0 auto;

		img {
			width: 100%;
			height: 100%;
		}

		.two-code-line {
			position: absolute;
			z-index: 2;
			right: 58px;
			width: 68px;
			height: 2px;
			background: url('../images/line.png') no-repeat;
			background-size: 68px 2px;
			animation: qrcode-line 2s infinite ease;
		}
	}

	.btn-gradual-yellow {
		position: relative;
		top: 61%;
		margin-top: 0;
	}
	@media screen and (max-width: 320px) {
		.btn-gradual-yellow {
			top: 68%;
		}
	}
}

.logo {
	width: 54px;
	height: 70px;
	margin: 15px 0 0 25px;
	background: url('../images/logo.png') no-repeat;
	background-size: 54px 70px;
}

.win-img {
	width: 190px;
	height: 120px;
	margin: -20px auto 10px;
	background: url('../images/winer.png') no-repeat;
	background-size: 190px 120px;
}

.nowin-img {
	width: 125px;
	height: 200px;
	margin: -20px auto 10px;
	background: url('../images/nowin.png') no-repeat;
	background-size: 125px 200px;
}

.nowin {
	color: #fff;
	text-align: center;

	b {
		color: #eb1b29;
		font-size: 18px;
	}
}

.pepsi-ad {
	margin-top: 50px;
	color: #fff;
	text-align: center;

	.img1 {
		width: 20px;
		height: 24px;
	}
}

@keyframes qrcode-line {
	0% {
		top: 35px;
		opacity: 0;
	}

	50% {
		opacity: 1;
	}

	100% {
		top: 95px;
		opacity: 0;
	}
}
